.advantages {
  counter-reset: advantages;

  margin: 0 auto 53px;
  padding: 0;
  width: @mobile-width-only - @mobile-gap - @mobile-gap;

  @media (min-width: @tablet-width) {
    width: 92%;
    max-width: @desktop-width - @desktop-gap - @desktop-gap;

    margin-bottom: 47px;
  }

  @media (min-width: @desktop-width) {
    margin-bottom: 26px;
  }
}

.advantage__header {
  margin: 0 0 40px;

  font-family: @font;
  font-style: @font-style;
  font-weight: @font-weight;
  font-size: 36px;
  line-height: 36px;
  color: @font-color;

  @media (min-width: @tablet-width) {
    margin: 0 0 71px;

    font-size: 60px;
    line-height: 60px;
  }

  @media (min-width: @desktop-width) {
    margin: 0 0 72px;
  }
}

.advantages__list {
  padding: 0;
  margin: 0;
  list-style: none;

  display: flex;
  flex-direction: row;
  justify-content: space-between;
  flex-wrap: wrap;
  row-gap: 20px;

  @media (min-width: @tablet-width) {
    column-gap: 22%;
    row-gap: 77px;
  }

  @media (min-width: 1000px) {
    column-gap: 70px;
  }
}

.advantages__item {
  position: relative;
  padding-left: 100px;
  height: 80px;

  z-index: 2;

  @media (min-width: @tablet-width) {
    height: auto;
    padding: 111px 0 57px;
    text-align: left;
  }

  &::before {
    display: none;

    @media (min-width: @tablet-width) {
      display: block;

      position: absolute;
      top: -40px;
      left: 50%;

      counter-increment: advantages;
      content: counter(advantages);
      z-index: 1;

      font-family: @font;
      font-style: @font-style;
      font-weight: @font-weight;
      font-size: 280px;
      line-height: 280px;
      color: hsl(0, 0%, 95%);
    }
  }
}

.advantages__description {
  padding: 5px 0 0;
  margin: 0;

  position: relative;
  z-index: 2;

  @media (min-width: @tablet-width) {
    padding: 0;
  }
}

.advantages__item-nutritional {
  background: url("../img/icon-nutrition.svg") no-repeat left;
  background-position-y: 0;

  @media (min-width: @tablet-width) {
    width: 230px;
  }
}

.advantages__item-easy-to-use {
  background: url("../img/icon-easy-to-use.svg") no-repeat left;
  background-position-y: 0;

  @media (min-width: @tablet-width) {
    width: 246px;
  }
}

.advantages__item-fast-tu-use {
  background: url("../img/icon-ready-to-use.svg") no-repeat left;
  background-position-y: 0;

  @media (min-width: @tablet-width) {
    width: 247px;
  }
}

.advantages__item-results {
  background: url("../img/icon-fast-result.svg") no-repeat left;
  background-position-y: 0;

  @media (min-width: @tablet-width) {
    width: 249px;
  }
}
